import { sbcUtils } from "./sbcUtils.js";

// eslint-disable-next-line no-unused-vars
import * as sbcTypes from "./sbcTypes.js";

/* ------------------------------------ */
/* Roll Bonuses module integration Lib  */
/* ------------------------------------ */
export class RollBonusOps {
  static flagKey = "ckl-roll-bonuses";

  static allTargets = [
    "armorFocus",
    "improvedArmorFocus",
    "elementalFocus",
    "greaterElementalFocus",
    "mythicElementalFocus",
    "furiousFocus",
    "martialFocus",
    "snakeSidewind",
    "spellFocus",
    "greaterSpellFocus",
    "mythicSpellFocus",
    "spellSpecialization",
    "vitalStrike",
    "improvedVitalStrike",
    "greaterVitalStrike",
    "weaponFocus",
    "weaponSpecialization",
    "greaterWeaponFocus",
    "greaterWeaponSpecialization",
    "mythicWeaponFocus",
    "fighterWeaponTraining",
    "versatilePerformance",
    "improvedCritical",
    "ammunition",
  ];

  /** @type {FlagSetup} */
  static flagSetup = {
    armorFocus: { boolean: "armor-focus", module: "armor-focus" },
    improvedArmorFocus: { boolean: "armor-focus-improved", module: "armor-focus-improved" },
    weaponFocus: { boolean: "weapon-focus", module: "weapon-focus" },
    greaterWeaponFocus: { boolean: "weapon-focus-greater", module: "weapon-focus-greater" },
    mythicWeaponFocus: { boolean: "weapon-focus-mythic", module: "weapon-focus-mythic" },
    weaponSpecialization: { boolean: "weapon-specialization", module: "weapon-specialization" },
    greaterWeaponSpecialization: { boolean: "weapon-specialization-greater", module: "weapon-specialization-greater" },
    martialFocus: { boolean: "martial-focus", module: "martial-focus" },
    elementalFocus: { boolean: "elemental-focus", module: "elemental-focus" },
    spellFocus: { boolean: "spell-focus", module: "spell-focus" },
    greaterSpellFocus: { boolean: "spell-focus-greater", module: "spell-focus-greater" },
    mythicSpellFocus: { boolean: "spell-focus-mythic", module: "spell-focus-mythic" },
    spellSpecialization: { boolean: "spell-specialization", module: "spell-specialization" },
    furiousFocus: { boolean: "furious-focus", module: "furious-focus" },
    snakeSidewind: { boolean: "snake-sidewind", module: "snake-sidewind" },
    vitalStrike: { boolean: "vital-strike", module: "vital-strike" },
    improvedVitalStrike: { boolean: "vital-strike-improved", module: "vital-strike-improved" },
    greaterVitalStrike: { boolean: "vital-strike-greater", module: "vital-strike-greater" },
    mythicVitalStrike: { boolean: "vital-strike-mythic", module: "vital-strike-mythic" },
    devastatingStrike: { boolean: "devastating-strike", module: "devastating-strike" },
    improvedDevastatingStrike: { boolean: "devastating-strike-improved", module: "devastating-strike-improved" },
    versatilePerformance: { boolean: "versatile-performance", module: "versatile-performance" },
  };

  static targets = [];

  static addTarget(target) {
    if (!RollBonusOps.targets.includes(target)) {
      RollBonusOps.targets.push(target);
    }
  }

  static versatilePerformanceOptions = {
    act: ["blf", "dis"],
    comedy: ["blf", "int"],
    dance: ["acr", "fly"],
    keyboard: ["dip", "int"],
    oratory: ["blf", "sen"],
    percussion: ["han", "int"],
    sing: ["blf", "sen"],
    string: ["blf", "dip"],
    wind: ["dip", "han"],
  };

  static favoredEnemyPlurals = {
    aberrations: "aberration",
    animals: "animal",
    constructs: "construct",
    dragons: "dragon",
    humanoids: "humanoid",
    "magical beasts": "magical beast",
    "monstrous humanoids": "monstrous humanoid",
    oozes: "ooze",
    outsiders: "outsider",
    plants: "plant",
    dwarves: "dwarf",
    elves: "elf",
    giants: "giant",
    goblinoids: "goblinoid",
    gnolls: "gnoll",
    gnomes: "gnome",
    halfings: "halfling",
    humans: "human",
    orcs: "orc",
    reptilians: "reptilian",
  };

  /**
   * Processes the targets for the given items.
   *
   * @param { ItemPF[] } items   - The items to process.
   * @returns {Promise<void>} - A promise that resolves when the processing is complete.
   */
  static async processTargets(items) {
    for (const item of items) {
      if (item.subType === "feat") {
        const target = sbcUtils.parseSubtext(item.name)[1];

        if (item.name.match(/^(?!.*Greater|Mythic).*Weapon Focus/i)) {
          await RollBonusOps.addSpecificBonus(item, "weaponFocus", target);
        } else if (item.name.match(/^(?!.*Greater).*Weapon Focus/i)) {
          await RollBonusOps.addSpecificBonus(item, "mythicWeaponFocus", target);
        } else if (item.name.match(/Weapon Focus/i)) {
          await RollBonusOps.addSpecificBonus(item, "greaterWeaponFocus", target);
        } else if (item.name.match(/^(?!.*Greater).*Weapon Specialization/i)) {
          await RollBonusOps.addSpecificBonus(item, "weaponSpecialization", target);
        } else if (item.name.match(/Weapon Specialization/i)) {
          await RollBonusOps.addSpecificBonus(item, "greaterWeaponSpecialization", target);
        } else if (item.name.match(/^(?!.*Improved).*Armor Focus/i)) {
          await RollBonusOps.addSpecificBonus(item, "armorFocus", target);
        } else if (item.name.match(/Armor Focus/i)) {
          await RollBonusOps.addSpecificBonus(item, "improvedArmorFocus", target);
        } else if (item.name.match(/^(?!.*Greater|Mythic).*Elemental Focus/i)) {
          await RollBonusOps.addSpecificBonus(item, "elementalFocus", target);
        } else if (item.name.match(/^(?!.*Greater).*Elemental Focus/i)) {
          await RollBonusOps.addSpecificBonus(item, "mythicElementalFocus", target);
        } else if (item.name.match(/Elemental Focus/i)) {
          await RollBonusOps.addSpecificBonus(item, "greaterElementalFocus", target);
        } else if (item.name.match(/Martial Focus/i)) {
          await RollBonusOps.addSpecificBonus(item, "martialFocus", target);
        } else if (item.name.match(/^(?!.*Greater|Mythic).*Spell Focus/i)) {
          await RollBonusOps.addSpecificBonus(item, "spellFocus", target);
        } else if (item.name.match(/^(?!.*Greater).*Spell Focus/i)) {
          await RollBonusOps.addSpecificBonus(item, "mythicSpellFocus", target);
        } else if (item.name.match(/Spell Focus/i)) {
          await RollBonusOps.addSpecificBonus(item, "greaterSpellFocus", target);
        } else if (item.name.match(/^(?!.*Greater).*Spell Specialization/i)) {
          await RollBonusOps.addSpecificBonus(item, "spellSpecialization", target);
        } else if (item.name.match(/Furious Focus/i)) {
          await RollBonusOps.addSpecificBoolean(item, "furiousFocus");
        } else if (item.name.match(/Snake Sidewind/i)) {
          await RollBonusOps.addSpecificBoolean(item, "snakeSidewind");
        } else if (item.name.match(/Improved Critical/i)) {
          await RollBonusOps.addImprovedCritical(item, target);
        } else if (item.name.match(/^(?!.*Greater|Improved|Mythic).*Vital Strike/i)) {
          await RollBonusOps.addSpecificBoolean(item, "vitalStrike");
        } else if (item.name.match(/^(?!.*Greater|Mythic).*Vital Strike/i)) {
          await RollBonusOps.addSpecificBoolean(item, "improvedVitalStrike");
        } else if (item.name.match(/^(?!.*Mythic).*Vital Strike/i)) {
          await RollBonusOps.addSpecificBoolean(item, "greaterVitalStrike");
        } else if (item.name.match(/Vital Strike/i)) {
          await RollBonusOps.addSpecificBoolean(item, "mythicVitalStrike");
        } else if (item.name.match(/^(?!.*Improved).*Devastating Strike/i)) {
          await RollBonusOps.addSpecificBoolean(item, "devastatingStrike");
        } else if (item.name.match(/Devastating Strike/i)) {
          await RollBonusOps.addSpecificBoolean(item, "improvedDevastatingStrike");
        }
      } else if (item.subType === "classFeat") {
        const target = sbcUtils.parseSubtext(item.name)[1];

        if (item.name.match(/Versatile Performance/i) && target) {
          await RollBonusOps.addVersatilePerformance(item, target.toLowerCase());
        } else if (item.name.match(/Weapon Training/i) && target) {
          await RollBonusOps.addWeaponTraining(item, target);
        } else if (item.name.match(/Favored Enemy/i) && target) {
          await RollBonusOps.addFavoredEnemy(item, target);
        } else if (item.name.match(/Favored Terrain/i) && target) {
          //await RollBonusOps.addFavoredTerrain(item, target);
        }
      } else if (item.system.subType === "ammo") {
        await RollBonusOps.addAmmoProperties(item);
      }
    }
  }

  /**
   * Adds ammo properties to the item, such as enhancement bonus, bane, flaming, etc.
   *
   * @param {ItemPF} item - The item to which the properties are applied.
   * @returns {Promise<void>} - A promise that resolves when the properties are added.
   */
  static async addAmmoProperties(item) {
    const updates = {};
    const name = item.name.toLowerCase();
    const [enemyType, enemySubType] = sbcUtils.extractCreatureTypeInfo(name);
    const damages = [];

    // Check if the item is a bane ammo
    if (item.name.match(/bane/i)) {
      Object.assign(updates, {
        "ammo-bane-creature-type": [enemyType],
        "ammo-bane-creature-subtype": enemySubType ? [enemySubType] : [],
      });
    }

    const m = name.match(/\+[12345]/);
    // Check if the item has an enhancement bonus
    const enhancementValue = m?.[0] || 0;
    if (enhancementValue) {
      Object.assign(updates, {
        "ammo-enhancement": `${enhancementValue.replace("+", "")}`,
      });
    }

    // Check for Flaming or Flaming Burst
    if (name.match(/flaming/i)) {
      damages.push({ formula: "1d6[Flaming]", types: ["fire"], crit: "nonCrit" });

      if (name.match(/flaming burst/i)) {
        damages.push({ formula: "1d10[Flaming Burst]", types: ["fire"], crit: "crit" });
      }
    }

    // Check for Frost or Icy Burst
    if (name.match(/frost/i)) {
      damages.push({ formula: "1d6[Frost]", types: ["cold"], crit: "nonCrit" });

      if (name.match(/icy burst/i)) {
        damages.push({ formula: "1d10[Icy Burst]", types: ["cold"], crit: "crit" });
      }
    }

    // Check for Shock or Shocking Burst
    if (name.match(/shock/i)) {
      damages.push({ formula: "1d6[Shock]", types: ["electricit"], crit: "nonCrit" });

      if (name.match(/shocking burst/i)) {
        damages.push({ formula: "1d10[Shocking Burst]", types: ["electricit"], crit: "crit" });
      }
    }

    // Check for Corrosive or Corrosive Burst
    if (name.match(/corrosive/i)) {
      damages.push({ formula: "1d6[Corrosive]", types: ["acid"], crit: "nonCrit" });

      if (name.match(/corrosive burst/i)) {
        damages.push({ formula: "1d10[Corrosive Burst]", types: ["acid"], crit: "crit" });
      }
    }

    // Add the damage types to the updates object
    if (damages.length > 0) {
      Object.assign(updates, {
        "ammo-damage": damages,
      });
    }

    await item.update({
      flags: {
        [RollBonusOps.flagKey]: updates,
      },
    });
  }

  /**
   *
   * @param {ItemPF} item - The item to which the bonus is applied.
   * @param {string} key - The key of the bonus to be added.
   * @param {string|number} target - The target to which the bonus is applied.
   * @returns {Promise<void>} - A promise that resolves when the bonus is added.
   * @throws {Error} - Throws an error if the key is not valid or if the target is not valid.
   */
  static async addSpecificBonus(item, key, target) {
    try {
      if (RollBonusOps.allTargets.includes(key)) {
        if (!target) {
          console.warn(`SBC: ${key} doesn not have a valid target.`);
          return;
        }

        await item.addItemBooleanFlag(RollBonusOps.flagSetup[key].boolean);
        await item.setFlag(RollBonusOps.flagKey, RollBonusOps.flagSetup[key].module, target);
      } else {
        console.warn(`SBC: ${key} is not a valid bonus.`);
      }
    } catch (err) {
      console.log(err);
    }
  }

  /**
   *
   * @param {ItemPF} item - The item to which the bonus is applied.
   * @param {string} key - The key of the bonus to be added.
   * @returns {Promise<void>} - A promise that resolves when the bonus is added.
   * @throws {Error} - Throws an error if the key is not valid or if the target is not valid.
   */
  static async addSpecificBoolean(item, key) {
    if (RollBonusOps.allTargets.includes(key)) {
      await item.addItemBooleanFlag(RollBonusOps.flagSetup[key].boolean);
    } else {
      console.warn(`SBC: ${key} is not a valid bonus.`);
    }
  }

  /**
   *
   * @param {ItemPF} item - The item to which the bonus is applied.
   * @param {string} target - The target to which the bonus is applied.
   * @returns {Promise<void>} - A promise that resolves when the bonus is added.
   * @throws {Error} - Throws an error if the key is not valid or if the target is not valid.
   */
  static async addImprovedCritical(item, target) {
    if (!target) {
      console.warn("SBC: Improved Critical doesn not have a valid target.");
      return;
    }

    await item.update({
      system: {
        flags: {
          boolean: {
            bonus_crit: true,
            "target_weapon-type": true,
          },
        },
      },
      flags: {
        [RollBonusOps.flagKey]: {
          "bonus_crit-keen": true,
          "target_weapon-type": [target],
        },
      },
    });
  }

  /**
   *
   * @param {ItemPF} item - The item to which the bonus is applied.
   * @param {string} target - The target to which the bonus is applied.
   * @returns {Promise<void>} - A promise that resolves when the bonus is added.
   * @throws {Error} - Throws an error if the key is not valid or if the target is not valid.
   */
  static async addVersatilePerformance(item, target) {
    if (!target) {
      console.warn("SBC: Versatile Performance doesn not have a valid target.");
      return;
    }

    const options = RollBonusOps.versatilePerformanceOptions[target];
    if (options) {
      /** @type {ActorPF} */
      const actor = item.actor;
      const baseSkill = actor.allSkills.find(
        (s) => s.startsWith("prf.") && actor.getSkillInfo(s).name.toLowerCase() === target.toLowerCase()
      );
      if (!baseSkill) {
        console.warn(`SBC: Perform (${target}) skill could not be found.`);
        return;
      }

      await item.addItemBooleanFlag(RollBonusOps.flagSetup["versatilePerformance"].boolean);
      await item.setFlag(RollBonusOps.flagKey, RollBonusOps.flagSetup["versatilePerformance"].module, [
        { base: baseSkill, choice1: options[0], choice2: options[1], expanded: "" },
      ]);
    }
  }

  /**
   *
   * @param {ItemPF} item  - The item to which the bonus is applied.
   * @param {string} target - The target to which the bonus is applied.
   * @returns {Promise<void>} - A promise that resolves when the bonus is added.
   * @throws {Error} - Throws an error if the key is not valid or if the target is not valid.
   */
  static async addWeaponTraining(item, target) {
    if (!target) {
      console.warn("SBC: Weapon Training doesn not have a valid target.");
      return;
    }

    const weaponBonus = target.match(/\+(\d+)/)[1];
    const weaponGroup = target
      .match(/(.*)\s\+\d+/)[1]
      .toLowerCase()
      .replace("heavy blades", "bladesHeavy")
      .replace("light blades", "bladesLight");

    await item.update({
      system: {
        flags: {
          boolean: {
            bonus_attack: true,
            bonus_damage: true,
            "target_weapon-group": true,
          },
        },
      },
      flags: {
        [RollBonusOps.flagKey]: {
          bonus_attack: weaponBonus,
          "bonus_attack-type": "untyped",
          bonus_damage: [],
          "bonus_damage-change": [{ formula: weaponBonus, type: "untyped" }],
          "target_weapon-group": [weaponGroup],
        },
      },
    });
  }

  static async addFavoredEnemy(item, target) {
    if (!target) {
      console.warn("SBC: Weapon Training doesn not have a valid target.");
      return;
    }

    /** @type {[string, number]} */
    let [enemyGroup, enemyBonus] = target.split("+").map((i) => i.trim());
    enemyGroup = enemyGroup.toLowerCase();
    for (const [key, value] of Object.entries(RollBonusOps.favoredEnemyPlurals)) {
      if (enemyGroup.includes(key)) {
        enemyGroup = enemyGroup.replace(key, value);
        break;
      }
    }

    const [enemyType, enemySubType] = sbcUtils.extractCreatureTypeInfo(enemyGroup);

    await item.update({
      system: {
        flags: {
          boolean: {
            bonus_attack: true,
            bonus_damage: true,
            "target_creature-type": true,
            "target_creature-subtype": true,
          },
        },
      },
      flags: {
        [RollBonusOps.flagKey]: {
          bonus_attack: enemyBonus,
          "bonus_attack-type": "untyped",
          bonus_damage: [],
          "bonus_damage-change": [{ formula: enemyBonus, type: "untyped" }],
          "target_creature-type": [enemyType],
          "target_creature-subtype": enemySubType ? [enemySubType] : [],
          target_toggle: "all",
        },
      },
    });
  }
}

/**
 * @typedef {object} Flag
 * @property {string} boolean - The boolean flag name.
 * @property {string} module - The module name.
 */

/**
 * @typedef {{[key: string]: Flag}} FlagSetup
 *
 * @property {string} boolean - The boolean flag name.
 * @property {string} module - The module name.
 */
